/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    box-sizing: border-box;
    margin: 0 /* rtl:ignore */;
    padding: 0 /* rtl:ignore */;
    border: 0 /* rtl:ignore */;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline;
}
b,
strong {
    font-weight: bold;
}
i,
em {
    font-style: italic;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.d2-ui-control-bar-dragflap-icon {
    visibility: hidden;
}

.left-controls {
    font-size: 16px;
    float: left;
    height: 36px;
}

.right-controls {
    float: right;
}

/* headers */
.dashboard-item-select h3 {
    font-size: 15px;
    color: #444;
    margin-top: 12px;
    margin-bottom: 10px;
}

/* list items */
.dashboard-item-select span p {
    font-size: 15px;
}

/* buttons */
.dashboard-item-select button {
    -webkit-align-self: flex-end;
            align-self: flex-end;
}

.dashboard-item-select button span {
    font-size: 13px !important;
    font-weight: 700 !important;
    color: #1565c0;
}

.dashboard-title:focus {
    color: #555;
}

.dashboard-description:focus {
    color: #777;
}

textarea {
    overflow: auto;
}

.item-action-button {
    border: none;
    background: transparent;
    cursor: pointer;
}

.item-action-button:focus {
    outline: 0;
}

.message-item {
    border-bottom: 1px solid #eceff1;
    padding-bottom: 10px;
    margin: 0 5px 10px 5px;
    height: 80px;
    overflow: hidden;
}

.message-item.view:hover {
    cursor: pointer;
}

.message-title {
    font-size: 15px;
    line-height: 18px;
    margin: 0 0 5px 0;
    color: #000000;
}

.message-title.unread {
    font-weight: bold;
}

.message-title.unread::before {
    content: '\25CF';
    color: #48a999;
    margin-right: 5px;
    position: relative;
    top: -1px;
}

/* react-grid-layout style */

.react-grid-item {
    overflow: hidden !important;
    box-shadow: 0px 0px 3px 0px #999999;
    background-color: #ffffff;
    border-radius: 3px;
    min-height: 70px;
    min-width: 70px;
}

.react-grid-item.edit,
.react-grid-item.RESOURCES,
.react-grid-item.TEXT,
.react-grid-item.MESSAGES,
.react-grid-item.APP {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.react-grid-item.edit:hover {
    cursor: move;
}

.react-grid-item.SPACER.view {
    visibility: hidden;
}

.react-grid-item.SPACER.edit {
    background-color: rgba(255, 255, 255, 0.3);
}

.react-grid-item.react-grid-placeholder {
    background: #000;
    opacity: 0.1;
    transition-duration: 150ms;
    z-index: 2;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

/* react-resizable */

.react-resizable-handle {
    background: none;
}

/* dashboard item - header */

.dashboard-item-header {
    font-size: 15px;
    font-weight: 700;
    color: #3a3a3a;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 4px 0 14px;
    height: 45px;
}

.dashboard-item-header-title {
    -webkit-flex: 1 1;
            flex: 1 1;
    overflow: hidden;
    position: relative;
    top: -1px;
    left: -1px;
}

/* dashboard item - content */

.dashboard-item-content {
    margin: 0 4px 4px;
    overflow: auto;
}

.react-grid-item.edit .dashboard-item-content,
.react-grid-item.RESOURCES .dashboard-item-content,
.react-grid-item.TEXT .dashboard-item-content,
.react-grid-item.MESSAGES .dashboard-item-content,
.react-grid-item.APP .dashboard-item-content {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.CHART.resizing .dashboard-item-content,
.EVENT_CHART.resizing .dashboard-item-content {
    overflow: hidden;
}

.d2ui-primary-button {
    border-radius: 2px;
    font-weight: 500;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24),
        0 0 8px 0 rgba(0, 0, 0, 0.12), 0 0 8px 0 rgba(0, 0, 0, 0.24);
}

.d2ui-primary-button.disabled {
    box-shadow: none;
}

body {
    background-color: #f4f6f8;
}

.app-wrapper {
    font-family: Roboto;
}

.dashboard-header-bar {
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
}

.dashboard-wrapper {
    background-color: #f4f6f8;
    padding-left: 10px;
    padding-right: 10px;
}

/* plugin TODO */

table.pivot * {
    -webkit-user-select: none !important;
    -moz-user-select: none !important;
    -ms-user-select: none !important;
    -o-user-select: none !important;
    user-select: none !important;
}

/* temporary workaround for headerbar */
.secondary-light-bg {
    background-color: #48a999;
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

